import {
	findTabbableNodes,
	getActiveElement
} from '@servicenow/library-enhanced-utils';
import inRange from 'lodash/inRange';

// JS `%` doesn't work with negative numbers :(
function mod(n, m) {
	return ((n % m) + m) % m;
}

export function focusFirstItemInPanel(panel) {
	const firstFocusableElement = findNextFocusTarget({
		within: panel,
		current: null
	});

	if (firstFocusableElement) {
		firstFocusableElement.focus();
	}
}

export function findNextFocusTarget({
	within = document.body,
	current = getActiveElement(),
	wrap = true,
	shiftKey = false
}) {
	if (!within) {
		return null;
	}

	const delta = shiftKey ? -1 : 1;
	const tabbableNodes = findTabbableNodes(within);
	const currentIndex = tabbableNodes.indexOf(current);
	const needsWrap = !inRange(currentIndex + delta, 0, tabbableNodes.length);
	if (needsWrap && !wrap) {
		return null;
	}

	const nextIndex = mod(currentIndex + delta, tabbableNodes.length);
	const next = tabbableNodes[nextIndex];
	return next || null;
}
