/**
 * Returns directional information for the given axis.
 * @param {("block"|"inline")} axis - Axis that is being calculated (block for top/bottom, inline for left/right)
 * @return {{topOrLeft: string, bottomOrRight: string, widthOrHeight: string}}
 */
export default function getAxisInfo(axis) {
	return {
		topOrLeft: axis === 'block' ? 'top' : 'left',
		bottomOrRight: axis === 'block' ? 'bottom' : 'right',
		widthOrHeight: axis === 'block' ? 'height' : 'width'
	};
}
