/**
 * Sets up an Observer that calls the given callback
 */
export default function createObserver(Observer) {
	const observed = new Map();

	return {
		add(id, target, callback, ...observerArgs) {
			const observer = new Observer(callback, ...observerArgs);
			observer.observe(target);
			observed.set(id, {observer, target});
		},

		remove(id) {
			const value = observed.get(id);
			if (!value) {
				return;
			}

			const {observer, target} = value;

			observer.unobserve(target);
			observed.delete(id);
		}
	};
}
