import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {layerEvents} from '@servicenow/library-enhanced-utils';
import {actionTypes} from '@servicenow/ui-core';
import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {getBestFitInfo, isTargetVisible} from './fit';
import isEqual from 'lodash/isEqual';
import once from 'lodash/once';
import noop from 'lodash/noop';
import setTailPosition from './utils/tail';
import {
	getContent,
	getFirstTrigger,
	isNestedPanel,
	findTriggerFromEvent,
	hoist,
	unhoist
} from './utils/dom';
import {findNextFocusTarget, focusFirstItemInPanel} from './utils/focus';
import createGlobalListener from './utils/global-event-listener';
import createObserver from './utils/observer';
import createScrollListener from './utils/scroll-listener';
import resolveFitProperties from './utils/resolve-fit-properties';
import ResizeObserver from 'resize-observer-polyfill';
import focusBehavior from '@servicenow/behavior-focus';

const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const onWindowResize = createGlobalListener('resize', {batch: true});
const clickListener = createGlobalListener('click');
const scrollListener = createScrollListener();
const intersectionObserver = createObserver(IntersectionObserver);
const resizeObserver = createObserver(ResizeObserver);

/**
 * Closes the popover. Removes any listeners that we've added and moves the
 * content back into the normal, non-hoisted location
 */
function closePopover({id, panel, host, dispatch}) {
	layerEvents.remove(id);
	clickListener.remove(id);
	onWindowResize.remove(id);
	scrollListener.remove(id);
	intersectionObserver.remove(id);

	const content = getContent(host);
	unhoist(panel, content);

	dispatch('NOW_POPOVER#CONTENT_HIDDEN', {});
}

/**
 * Opens the popover. Hoists the content nodes to the proper location and then
 * calls fit.
 */
async function openPopover({
	host,
	container,
	disableScroll,
	dispatch,
	updateState
}) {
	const content = getContent(host);

	// invalid markup. No content has been slotted
	if (content.length === 0) {
		return;
	}

	const panel = await hoist(host, container, content);
	panel.disableScroll = disableScroll;

	updateState({
		shouldRender: false,
		operation: 'assign',
		value: {
			panel,
			finishedOpening: false
		}
	});

	dispatch(() => ({type: 'PRIVATE#CHECK_FIT', payload: {}}));
}

function handleKeyDown(
	event,
	panel,
	trigger,
	interactionType,
	dispatch,
	updateState
) {
	// escape closes the popover and refocuses the trigger
	if (event.key === 'Escape') {
		event.preventDefault();
		event.stopPropagation();

		dispatch(() => ({
			type: 'NOW_POPOVER#OPENED_SET',
			payload: {value: false}
		}));

		// return false to ensure parent popovers don't also respond to Escape
		return false;
	}

	// tab moves focus, but the exact behavior depends on the interactionType
	if (event.key === 'Tab') {
		handleTabPress({
			event,
			panel,
			dispatch,
			trigger,
			interactionType,
			updateState
		});
	}
}

function handleClick(event, host, panel, dispatch, updateState) {
	const path = event.composedPath();

	// DEF0088795: The click event originates from the hoisted element in the
	// DOM. But if that hoisted component defines a Seismic eventHandler,
	// Seismic will re-emit that event from the original (unhoisted) content
	// host.
	//
	// So even though it feels like a single click event, there are actually 2
	// events being propagated, each with a different path. We need to know if
	// both of these events originate from the panel in order to correctly
	// _not_ dispatch OPENED_SET.
	//
	// `path` will include `panel` on the first click event, since the panel is
	// dynamically created and holds the hoisted element.
	//
	// `path` will include `host` on the 2nd click event, since the host is a
	// parent of the original (unhoisted) content node.
	//
	// We also need to guard against events coming from a nested popover panel
	// (e.g.: when we have a <now-select> in the content of a bigger
	// <now-popover>). These panels are hoisted independently, so the outer
	// panel is not in the composedPath of the inner panel.
	const hostInPath = path.some((element) => {
		return (
			element === host || element === panel || isNestedPanel(element, panel)
		);
	});

	if (!hostInPath) {
		updateState({
			shouldRender: false,
			operation: 'set',
			path: 'restoreFocus',
			value: false
		});
		dispatch(() => ({
			type: 'NOW_POPOVER#OPENED_SET',
			payload: {value: false}
		}));
	}
}

/**
 * Once the popover is opened and we've fit the panel, we want to set up a
 * bunch of listeners to track things like: outside clicks, tab navigation,
 * scrolling, and focus management.
 */
function handlePopoverOpened({
	id,
	host,
	panel,
	updateFit,
	target,
	content,
	trigger,
	container,
	dispatch,
	updateState,
	fitContent,
	fitContentArea,
	targetPosition,
	contentPosition,
	interactionType
}) {
	// refit any time the window is resized. This is not using `updateFit`
	// because we do want to allow the fit position to change (otherwise a resize
	// might make panel content inaccessible).
	onWindowResize.add(id, () => dispatch(() => ({type: 'PRIVATE#CHECK_FIT'})));

	clickListener.add(id, (event) => {
		handleClick(event, host, panel, dispatch, updateState);
	});
	layerEvents.add({
		id,
		events: ['keydown'],
		callback: (event) => {
			handleKeyDown(
				event,
				panel,
				trigger,
				interactionType,
				dispatch,
				updateState
			);
		}
	});

	// for dialog type popovers: we want to move focus to the first item in the
	// panel once the panel has been opened. However, we need to delay this until
	// the IntersectionObserver has fired and sets the visibility of contentArea
	// to visible.
	const moveFocusIfNeeded =
		interactionType === 'dialog' ? once(focusFirstItemInPanel) : noop;

	// When the target element is scrolled out-of-view, we want to stop showing
	// the panel. Similarly, when the target is scrolled back into view, we want
	// to show the panel again.
	//
	// Unlike Chrome/Firefox, Safari may not immediately trigger the callback
	// when the IntersectionObserver is first connected, so we can't rely on that
	// callback to initialize `targetVisible` or the correct styles. Since we
	// just called fit, assume that we're all good to display this until we get
	// an IntersectionObserver event that says we can't.
	let targetVisible = true;
	fitContentArea.style.display = 'block';
	fitContentArea.style.visibility = 'visible';

	intersectionObserver.add(
		id,
		target,
		(entries) => {
			targetVisible = isTargetVisible(targetPosition, entries[0]);
			fitContentArea.style.display = targetVisible ? 'block' : 'none';
			fitContentArea.style.visibility = targetVisible ? 'visible' : 'hidden';

			if (targetVisible) {
				moveFocusIfNeeded(panel);
			}
		},

		// these thresholds allow the intersection observer to fire on the
		// beginning, middle, and end of the target crossing the viewport. We want
		// these because depending on the target fit position, we need to check the
		// visibility of different points on the target.
		//
		// e.g.: if the target position is "top-center" and we are scrolling down,
		// we want to be able to check the visibility as soon as any part of the
		// target is falling off vertically (threshold = 0). When scrolling
		// horizontally, we can continue to show panel up until the middle point of
		// the target crosses the viewport (thresold = 0.5)
		{threshold: [0, 0.5, 1]}
	);

	// refit when scrolling, but force the position to remain the same
	scrollListener.add(id, host, container, () => {
		if (targetVisible) {
			updateFit();
		}
	});

	if (contentPosition.includes('center')) {
		resizeObserver.add(id, fitContent, () => {
			if (targetVisible) {
				updateFit();
			}
		});
	}

	// tell the world that we're all good to go :+1:
	dispatch(() => ({
		type: 'NOW_POPOVER#CONTENT_VISIBLE',
		payload: {
			content
		}
	}));
}

/**
 * Event handler for when the `Tab` key is pressed while the popover is opened.
 * Depending on the interactionType property we will focus different elements.
 */
const handleTabPress = ({
	event,
	trigger,
	panel,
	dispatch,
	interactionType,
	updateState
}) => {
	// when the popover does not contain interactive content, we can just let the
	// browser natively handle the Tab.
	if (interactionType === 'none') {
		updateState({
			shouldRender: false,
			operation: 'set',
			path: 'restoreFocus',
			value: false
		});
		dispatch(() => ({
			type: 'NOW_POPOVER#OPENED_SET',
			payload: {value: false}
		}));
		return;
	}

	// listbox interactions do not have a focus trap. shift-tab returns focus
	// back to the trigger element. Regular tab moves focus to the item after the
	// trigger, which can be handled natively. Regardless, the popover is closed
	if (interactionType === 'listbox') {
		const isTabInPanel = event.composedPath().includes(panel);
		if (event.shiftKey && trigger) {
			event.preventDefault();
		} else if (isTabInPanel) {
			event.preventDefault();
			updateState({
				shouldRender: false,
				operation: 'set',
				path: 'restoreFocus',
				value: true
			});
		} else {
			updateState({
				shouldRender: false,
				operation: 'set',
				path: 'restoreFocus',
				value: false
			});
		}
		dispatch('NOW_POPOVER#OPENED_SET', {value: false});
		return;
	}

	// dialog types trap focus within the content and keep the popover open
	if (interactionType === 'dialog') {
		event.preventDefault();
		const focusTarget = findNextFocusTarget({
			within: panel,
			shiftKey: event.shiftKey
		});

		if (focusTarget) {
			focusTarget.focus();
		}
	}
};

/**
 * Callback when the something in the `trigger` slot has been clicked. Need to
 * keep track of which trigger was clicked (in case multiple triggers are slotted
 * in), and then toggle the open state of the panel.
 */
const handleSlotClick = ({event, dispatch, updateState, slot}) => {
	const clickedTrigger = findTriggerFromEvent(event, slot);

	// this can happen if you click, drag, and release off one trigger and on to
	// another. In that case the composedPath no longer includes the original
	// trigger.
	if (!clickedTrigger) {
		return;
	}

	event.stopPropagation();

	// keep track of which trigger was clicked. This is needed to know where to
	// position the panel (when there is no explicit `position-target`) and to
	// track which element to restore focus to once the panel is closed.
	updateState({
		operation: 'set',
		path: 'clickedTrigger',
		value: clickedTrigger,
		shouldRender: false
	});

	dispatch(({properties}) => {
		return {
			type: 'NOW_POPOVER#OPENED_SET',
			payload: {
				value: !properties.opened
			}
		};
	});
};

const view = (state, {dispatch, updateState}) => {
	const {opened} = state.properties;

	const ref = createRef();

	return (
		<slot
			name="trigger"
			ref={ref}
			on-slotchange={
				opened ? () => dispatch(() => ({type: 'PRIVATE#CHECK_FIT'})) : undefined
			}
			on-click={(event) =>
				handleSlotClick({
					event,
					dispatch,
					updateState,
					slot: ref.current
				})
			}
		/>
	);
};

/**
 * A popover lets you float temporary contextual content over other content,
 * relative to a triggering element.
 *
 * Popover content is placed in a panel element, which optionally provides
 * a visual tail that points the content back to its triggering element.
 * Both the content and triggering elements are customizable.
 *
 * In order to correctly position the content, the popover will hoist the
 * content out of the normal DOM flow and append it to the body. Therefore, the
 * content element that is used must be a Now Experience UI Component in order
 * to correctly encapsulate styles and propagate actions.
 *
 * ```jsx
 * <now-popover>
 *	 <now-button slot="trigger" label="Check out my popover" />
 *	 <now-my-dialog slot="content" />
 * </now-popover>
 * ```
 *
 * `now-popover` elements define a `refit()` method that can be used to force
 * the popover to recalculate the position of the panel. This is useful when
 * something changes the layout or causes elements to move when the popover is
 * already open.
 *
 * @seismicElement now-popover
 * @summary Temporary contextual content that displays over other content when a user selects the triggering element.
 */
createEnhancedElement('now-popover', {
	properties: {
		/**
		 * Type of content displayed in the popover. These types are similar to
		 * ARIA roles and control how the popover manages focus.
		 *
		 * Valid values are:
		 * - "dialog": Content contains interactive content. Focus
		 *   stays within the content while the popover is opened.
		 *   An example of this is a calendar picker.
		 * - "listbox": Content contains a list of items that a user
		 *   can select from (typically using the arrow keys). Focus does not stay
		 *   within the content when the popover is opened. An example of this is
		 *   a dropdown.
		 * - "none": Content contains no interactive content.
		 *   An example of this is a tooltip.
		 *
		 * @type {("dialog"|"none"|"listbox")}
		 * @private
		 */
		interactionType: {
			default: 'dialog',
			schema: {type: 'string', enum: ['dialog', 'none', 'listbox']}
		},
		/**
		 * If true, the popover does not include a visual tail that points
		 * the content back to the trigger. Setting this property to true
		 * changes the default value of the `positions` property.
		 *
		 * See the `positions` property for more details.
		 * @type {boolean}
		 */
		hideTail: {default: false, schema: {type: 'boolean'}},
		/**
		 * The element that triggers the content. If not specified,
		 * the trigger slot triggers the content.
		 *
		 * @type {HTMLElement}
		 **/
		positionTarget: {},
		/**
		 * When true, the popover opens. The value of this property automatically updates
		 * when the user opens or closes the popover unless the `manage-opened` property is set.
		 * You can use the `manage-opened` property to override the default behavior and handle the
		 * `NOW_POPOVER#OPENED_SET` action manually.
		 *
		 * Note: Popover content may not be
		 * positioned and visible when `NOW_POPOVER#OPENED_SET`
		 * dispatches. See `NOW_POPOVER#CONTENT_VISIBLE` for
		 * an action that dispatches only when content is correctly
		 * positioned.
		 * @type {{value: boolean}}
		 */
		opened: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * A list of possible positions to place the content relative to its
		 * target, in order of preference. The content is placed in the
		 * position that fits best.
		 *
		 * Each object in `positions` specifies which points on the target and
		 * content elements align. The value of a point describes the
		 * block then inline location on the edge of the element:
		 *
		 *  - top-start
		 *  - top-center
		 *  - top-end
		 *  - center-end
		 *  - bottom-end
		 *  - bottom-center
		 *  - bottom-start
		 *  - center-start
		 *
		 * In a left-to-right language, `top-start` would be at the topmost and
		 * leftmost point of the element, with each successive value moving
		 * clockwise around the element.
		 *
		 * For example:
		 *
		 * The following aligns the bottom center of the content to the top center
		 * of the target, which is commonly used for tooltip-like content:
		 *
		 *   { target: 'top-center', content: 'bottom-center' }
		 *
		 * To have the content positioned to the right of the target (in a
		 * left-to-right language):
		 *
		 *   { target: 'center-end', content: 'center-start' }
		 *
		 * A common dropdown position, where content is aligned to the start of the
		 * target and opens below the target:
		 *
		 *   { target: 'bottom-start', content: 'top-start' }
		 *
		 *
		 * The default value depends on whether or not the popover includes a tail.
		 * With a tail, the default positions center the content and the trigger:
		 *
		 *  [
		 *    { target: 'top-center', content: 'bottom-center' },
		 *    { target: 'bottom-center', content: 'top-center' },
		 *    { target: 'center-end', content: 'center-start' },
		 *    { target: 'center-start', content: 'center-end' }
		 *  ]
		 *
		 * Without a tail, the default positions align the content and target
		 * along an axis:
		 *
		 * 	[
		 * 		{ target: 'bottom-start', content: 'top-start' },
		 * 		{ target: 'bottom-end', content: 'top-end' },
		 * 		{ target: 'top-start', content: 'bottom-start' },
		 * 		{ target: 'top-end', content: 'bottom-end' },
		 * 		{ target: 'top-end', content: 'top-start' },
		 * 		{ target: 'bottom-end', content: 'bottom-start' },
		 * 		{ target: 'top-start', content: 'top-end' },
		 * 		{ target: 'bottom-start', content: 'bottom-end' }
		 * 	];
		 *
		 * When overriding the default value, provide
		 * multiple position objects to this property. This practice allows for
		 * alternative placements in the case where the content does not
		 * completely fit in the preferred position.
		 *
		 * @type {Array<{target: string, content: string}>}
		 */
		positions: {
			schema: {
				type: 'array',
				items: {
					oneOf: [
						{type: 'string'},
						{
							type: 'object',
							properties: {
								target: {
									type: 'string',
									enum: [
										'top-start',
										'top-center',
										'top-end',
										'center-start',
										'center-end',
										'bottom-start',
										'bottom-center',
										'bottom-end'
									]
								},
								content: {
									type: 'string',
									enum: [
										'top-start',
										'top-center',
										'top-end',
										'center-start',
										'center-end',
										'bottom-start',
										'bottom-center',
										'bottom-end'
									]
								}
							},
							required: ['target']
						}
					]
				}
			}
		},
		/**
		 * Optional sizing constraints to enforce on the popover content. Valid keys are:
		 *
		 *  - width
		 *  - minWidth
		 *  - maxWidth
		 *  - height
		 *  - minHeight
		 *  - maxHeight
		 *
		 * The value for each key must be a positive integer representing a
		 * pixel value to apply to the corresponding content style.
		 * Alternatively, you can specify the string `"target"` to have the content
		 * match the value of the target element.
		 *
		 * For example, to ensure the content is at least 400x200 pixels:
		 *
		 *  {
		 *		"minWidth": 400,
		 *		"minHeight": 200
		 *  }
		 *
		 * To ensure that the content is always as wide as its target:
		 *
		 *	{
		 *		"width": "target"
		 *	}
		 *
		 * @private
		 * @type {PopoverConstrain}
		 */
		constrain: {
			schema: {
				type: 'object',
				properties: {
					width: {oneOf: [{const: 'target'}, {type: 'number'}]},
					minWidth: {oneOf: [{const: 'target'}, {type: 'number'}]},
					maxWidth: {oneOf: [{const: 'target'}, {type: 'number'}]},
					height: {oneOf: [{const: 'target'}, {type: 'number'}]},
					minHeight: {oneOf: [{const: 'target'}, {type: 'number'}]},
					maxHeight: {oneOf: [{const: 'target'}, {type: 'number'}]}
				}
			}
		},
		/**
		 * Number of pixels to shift the popover content away from the trigger.
		 * @type {number}
		 * @private
		 */
		offset: {schema: {type: ['array', 'number']}},
		/**
		 * Which element to hoist the popover panel to.
		 * @type {HTMLElement}
		 * @private
		 */
		container: {
			get default() {
				return document.body;
			}
		},
		/**
		 * If true, content in the popover panel will not overflow with a scrollbar.
		 * Use this flag if your popover content already contains a custom
		 * scroll container for handling overflowed content.
		 * @type {boolean}
		 */
		disableScroll: {schema: {type: 'boolean'}}
	},
	initialState: {
		/**
		 * Whether or not the popover should restore
		 * focus to the trigger element when the popover
		 * is closed
		 */
		restoreFocus: true,
		/**
		 * A reference to the `now-popover-panel` element that is dynamically
		 * created when the popover is opened.
		 */
		panel: undefined,
		/**
		 * Because Seismic dispatches and renders are asynchronous, the panel and
		 * its contents are not rendered and hoisted immediately when the `opened`
		 * prop is set. This state value tracks when we know that the DOM is in a
		 * good spot and we can add event listeners and fire
		 * `NOW_POPOVER#CONTENT_VISIBLE`
		 */
		finishedOpening: false,
		/**
		 * When multiple triggers are slotted in, this value tracks which one of those
		 * triggers is responsible for causing the popover to be opened.
		 */
		clickedTrigger: undefined
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: function handleConnected({
			updateState,
			dispatch,
			properties,
			host,
			updateProperties
		}) {
			// In standalone HTML apps, the Seismic default value for `container`
			// is evaluated before `document.body` exists
			if (!properties.container) {
				updateProperties({container: document.body});
			}

			// Adds a public `refit()` method on the host of the popover, which will
			// force the popover to re-run fit. This works kind of like the focus
			// behavior, where we're adding public methods to give the consumer control
			// over when to do something that doesn't really fit in the props-down
			// actions-up model.
			host.refit = () => dispatch('PRIVATE#CHECK_FIT');

			if (properties.opened) {
				openPopover({
					host,
					container: properties.container,
					disableScroll: properties.disableScroll,
					dispatch,
					updateState
				});
			}
		},
		[COMPONENT_DISCONNECTED]: function handleDisconnected({
			action,
			dispatch,
			state,
			properties
		}) {
			if (properties.opened) {
				closePopover({
					panel: state.panel,
					id: action.meta.id,
					host: action.payload.host,
					dispatch
				});
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: function handlePropertyChanged({
			action,
			dispatch,
			properties,
			state,
			updateState
		}) {
			const {host, name, value, previousValue} = action.payload;

			// If a property related to fit changes we need to re-run fit
			if (
				name === 'positions' ||
				name === 'offset' ||
				name === 'constrain' ||
				(name === 'positionTarget' && previousValue)
			) {
				// Deep equality check because non-scalar props (offset, position,
				// etc) might change all the time and this is cheaper than recalcing
				// all of fit.
				if (!isEqual(value, previousValue)) {
					dispatch(() => ({type: 'PRIVATE#CHECK_FIT'}));
				}

				return;
			}

			if (name === 'opened') {
				const {id} = action.meta;
				const {restoreFocus} = state;

				if (value) {
					openPopover({
						host,
						container: properties.container,
						disableScroll: properties.disableScroll,
						dispatch,
						updateState
					});
					updateState({
						shouldRender: false,
						operation: 'set',
						path: 'restoreFocus',
						value: true
					});
				} else {
					closePopover({
						id,
						host,
						panel: state.panel,
						dispatch
					});
					const trigger = state.clickedTrigger || getFirstTrigger(host);
					if (restoreFocus && trigger) {
						trigger.focus();
					}
				}
			}
		},
		/**
		 * Checks to see if we have everything we need to run fit. If so,
		 * do it and apply the styles to the panel.
		 */
		'PRIVATE#CHECK_FIT': {
			stopPropagation: true,
			modifier: {
				type: 'throttle',
				limit: 10
			},
			effect: ({host, state, action, dispatch, properties, updateState}) => {
				const {panel, clickedTrigger} = state;
				const {
					dir,
					opened,
					container,
					positionTarget,
					interactionType
				} = properties;
				const content = getContent(host);
				const trigger = clickedTrigger || getFirstTrigger(host);
				const target = positionTarget || trigger;
				const fitContentArea =
					panel && panel.shadowRoot.querySelector('#fit-content-area');
				const fitContent =
					panel && panel.shadowRoot.querySelector('#fit-content');

				if (!opened || !target || !content || !fitContentArea || !fitContent) {
					return;
				}

				// Switching from content center position to non center position while
				// open can cause incorrect calculations.
				fitContent.removeAttribute('style');

				// set up a smaller, more focused fit function that we can call on
				// scroll or content resize. We don't want to go through the entire
				// PRIVATE#CHECK_FIT action for a couple reasons:
				//
				// - dispatched actions are scheduled, causing a somewhat noticeable
				// delay between the target moving and the panel refitting to catch up.
				//
				// - when scrolling: we don't want to let the fit position of the panel
				// change, because it's a poor user experience when the panel moves
				// around a lot after opening.

				let fitProps = {
					...resolveFitProperties({
						dir,
						positions: properties.positions,
						constrain: properties.constrain,
						hideTail: properties.hideTail,
						offset: properties.offset
					}),
					target,
					container,
					content: fitContent,
					contentArea: fitContentArea
				};

				const updateFit = () => {
					// run fit calc
					const fitInfo = getBestFitInfo({...fitProps});

					// apply styles from fitInfo
					Object.assign(fitContentArea.style, fitInfo.style.contentArea);
					Object.assign(fitContent.style, fitInfo.style.content);

					// position tail
					if (!properties.hideTail) {
						const {dimensions, targetPosition, contentPosition} = fitInfo;
						setTailPosition({
							contentArea: fitContentArea,
							targetPosition,
							contentPosition,
							targetDimensions: dimensions.target,
							panel: state.panel,
							contentAreaDimensions: dimensions.contentArea
						});
					}

					return fitInfo;
				};

				const fitInfo = updateFit();

				// Let the first call to fit calculate the position and size of the
				// content. When we call updateFit again on scroll, we want to force
				// the same position and minimum content size
				fitProps.positions = [
					[fitInfo.targetPosition, fitInfo.contentPosition]
				];

				// set up listeners and handle focus, but only when we first open.
				// don't want to redo this stuff if PRIVATE#CHECK_FIT is called again
				// while the panel is already open.
				if (!state.finishedOpening) {
					handlePopoverOpened({
						id: action.meta.id,
						host,
						panel,
						target,
						updateFit,
						content,
						trigger,
						container,
						dispatch,
						updateState,
						fitContent,
						fitContentArea,
						targetPosition: fitInfo.targetPosition,
						contentPosition: fitInfo.contentPosition,
						interactionType
					});

					updateState({
						shouldRender: false,
						operation: 'set',
						path: 'finishedOpening',
						value: true
					});
				}
			}
		}
	},

	dispatches: {
		/**
		 * Dispatched when a user opens or closes the popover. Payload contains a
		 * `value` property, which is `true` when the popover opens and
		 * `false` when the popover closes.
		 *
		 * Note: Because rendering is asynchronous, the popover content DOM may not be in
		 * the correct state when this action is dispatched.
		 *
		 * Use `NOW_POPOVER#CONTENT_VISIBLE` and `NOW_POPOVER#CONTENT_HIDDEN` actions
		 * to run additional logic once the popover content is rendered in or
		 * removed from the DOM respectively. Set the `manage-opened` property to override the default
		 * behavior and handle this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_POPOVER#OPENED_SET': {},

		/**
		 * Dispatched when the popover opens and renders the content. Unlike
		 * `NOW_POPOVER#OPENED_SET`, this action only fires after the popover
		 * content is rendered in the DOM. The `content` property contains the
		 * slotted popover content.
		 * @type {{content: Array<HTMLElement>}}
		 * @private
		 */
		'NOW_POPOVER#CONTENT_VISIBLE': {},

		/**
		 * Dispatched when the popover content has been closed and removed. Unlike
		 * `NOW_POPOVER#OPENED_SET`, this action only fires after the popover
		 * content is fully removed from the DOM.
		 * @type {{}}
		 * @private
		 */
		'NOW_POPOVER#CONTENT_HIDDEN': {}
	},
	slots: {
		/**
		 * The trigger element that the popover content is positioned in relation to.
		 * If the `manage-opened` property is not set, then clicking on this slot will
		 * toggle the opened state of the popover.
		 */
		trigger: {},

		/**
		 * The popover content that is visible when the `opened` property
		 * is true. This *must* be a component and cannot inherit
		 * styles from a parent element. If the content is not interactive,
		 * you should wrap it in an `aria-live` region so that a
		 * screenreader announces it when the popover opens.
		 */
		content: {}
	},
	behaviors: [{behavior: focusBehavior}],
	view
});
