/**
 * Calculates constrain styles based on developer-specified configuration.
 */
export function getConstrainStyles(constrain) {
	return Object.entries(constrain).reduce((result, [key, value]) => {
		result[key] = `${value}px`;
		return result;
	}, {});
}

/**
 * Calculates and returns styles to be applied to the content.
 * @param {Object} contentStyles - Other content styles to apply
 * @param {Object} contentPosition - position of content
 * @return {Object}
 */
export function addCSSProperties(contentStyles, contentPosition) {
	const isBottomAligned = contentPosition[0] === 'bottom';
	const isEndAligned = contentPosition[1] === 'end';

	return {
		...contentStyles,
		top: isBottomAligned ? 'unset' : '0px',
		bottom: isBottomAligned ? '0px' : 'unset',
		left: isEndAligned ? 'unset' : '0px',
		right: isEndAligned ? '0px' : 'unset',
		position: 'absolute',
		pointerEvents: 'initial'
	};
}

/**
 * Calculates and returns styles to be applied to the content.
 * @param {Position} contentPosition - Logical position for the content
 * @param {Constrain} constrain - normalized contrain configuration object
 * @return {Object} a set of CSS properties that should be applied to the
 * content element.
 */
export default function calculateContentStyles(contentPosition, constrain) {
	const contentStyles = getConstrainStyles(constrain);
	return addCSSProperties(contentStyles, contentPosition);
}
