const TAIL_LENGTH = 20;

export default function setTailPosition({
	panel,
	contentArea,
	targetDimensions,
	contentAreaDimensions,
	targetPosition,
	contentPosition
}) {
	const {offset, direction} = getTailPosition({
		targetDimensions,
		contentAreaDimensions,
		targetPosition,
		contentPosition
	});

	contentArea.style.setProperty('--popover-tail', `${offset}px`);
	panel.tailDirection = direction;
}

export function getTailPosition({
	targetDimensions,
	contentAreaDimensions,
	targetPosition,
	contentPosition
}) {
	if (
		(targetPosition[0] === 'bottom' && contentPosition[0] === 'top') ||
		(targetPosition[0] === 'top' && contentPosition[0] === 'bottom')
	) {
		const middleOfTarget = targetDimensions.width / 2;
		const deltaX = targetDimensions.left - contentAreaDimensions.left;
		return {
			direction: targetPosition[0] === 'top' ? 'down' : 'up',
			offset: deltaX + middleOfTarget - TAIL_LENGTH / 2
		};
	}

	if (
		(targetPosition[1] === 'start' && contentPosition[1] === 'end') ||
		(targetPosition[1] === 'end' && contentPosition[1] === 'start')
	) {
		const middleOfTarget = targetDimensions.height / 2;
		const deltaY = targetDimensions.top - contentAreaDimensions.top;
		return {
			direction: targetPosition[1] === 'start' ? 'right' : 'left',
			offset: deltaY + middleOfTarget - TAIL_LENGTH / 2
		};
	}

	return null;
}
