import {findAncestors} from './dom';

/**
 * Attaches scroll listeners to all ancestor elements of the given host element
 * that have shadow roots (because scroll events do not automatically bubble
 * through shadow boundaries). When any of the scroll listeners are fired,
 * trigger the callback function.
 * @param {HTMLElement} host
 * @param {string} componentId
 * @param {function} callback
 */
export default function createScrollListener() {
	const attached = new Map();

	return {
		async add(id, host, container, callback) {
			const ancestors = await findAncestors(id, host, container);
			const targets = [];
			ancestors.forEach((el) => el.shadowRoot && targets.push(el));

			const listener = (e) => {
				const [source] = e.composedPath();
				if (ancestors.has(source)) {
					callback(e);
				}
			};

			for (const target of targets) {
				target.shadowRoot.addEventListener('scroll', listener, true);
			}

			attached.set(id, {targets, listener});
		},
		remove(id) {
			const value = attached.get(id);
			if (!value) {
				return;
			}

			const {targets, listener} = value;
			for (const target of targets) {
				target.shadowRoot.removeEventListener('scroll', listener, true);
			}

			attached.delete(id);
		}
	};
}
