import getAxisInfo from './get-axis-info';

function isVisible(axis, position, targetRect, intersectionRect) {
	const {topOrLeft, bottomOrRight, widthOrHeight} = getAxisInfo(axis);

	// When centered, we want to make sure that the size of the intersection is
	// at least as long as half of the target
	if (position === 'center') {
		return (
			intersectionRect[bottomOrRight] - intersectionRect[topOrLeft] >=
			targetRect[widthOrHeight] / 2
		);
	}

	// Otherwise we just need to check that the intersection starts/ends in the
	// same place that our target does.
	//
	// ( Safari will return fractional pixels for the targetRect, but
	// not for the intersectionRect, so it's possible for these numbers to be
	// slightly different
	return (
		Math.round(targetRect[position]) === Math.round(intersectionRect[position])
	);
}

/**
 * Checks to see if the origin point of a target element is visible based on
 * the fit position.
 * @param targetPosition {Array<string>} the target's fit position
 * @param entry {IntersectionObserverEntry} an entry that is created by an IntersectionObserver
 *
 * @returns {boolean} true when the origin point of the entry is still visible
 */
export default function isTargetVisible(targetPosition, entry) {
	const {
		intersectionRatio,
		boundingClientRect: targetRect,
		intersectionRect
	} = entry;

	// 0% of the target is visible
	if (intersectionRatio === 0) {
		return false;
	}

	// 100% of the target is visible
	if (intersectionRatio === 1) {
		return true;
	}

	const blockPosition = targetPosition[0];
	const inlinePosition = targetPosition[1]
		.replace('start', 'left') // convert start/end to left/right so that these values
		.replace('end', 'right'); // match up with the properties we get in a DOMRect

	return (
		isVisible('block', blockPosition, targetRect, intersectionRect) &&
		isVisible('inline', inlinePosition, targetRect, intersectionRect)
	);
}
