import getAxisInfo from './get-axis-info';

/**
 * Calculates either the height or width based on the target and content
 * position and dimensions.
 * @param {Dimensions} targetDimensions - Target dimensions object plus offsets
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} containerDimensions - Container dimensions object
 * @param {("top"|"start"|"center"|"bottom"|"end")} targetPosition - Logical target position
 * @param {("top"|"start"|"center"|"bottom"|"end")} contentPosition - Logical content position
 * @param {("block"|"inline")} axis - Axis that is being calculated (block for top/bottom, inline for left/right)
 * @return {number} - the calculated width or height of the content area
 */
export function calculateContentAreaLength(
	targetDimensions,
	contentDimensions,
	containerDimensions,
	targetPosition,
	contentPosition,
	axis
) {
	const {topOrLeft, widthOrHeight} = getAxisInfo(axis);

	// edge case: if centered content is bigger than the container, we want
	// contentArea to fill the entire container.
	if (
		contentPosition === 'center' &&
		contentDimensions[widthOrHeight] >=
			containerDimensions.scroll[widthOrHeight]
	) {
		return containerDimensions.scroll[widthOrHeight];
	}

	const targetOffsetContainerTopOrLeft =
		targetDimensions[topOrLeft] -
		containerDimensions[topOrLeft] +
		containerDimensions.scroll[axis];

	// Initial length is the distance between the top/left of the container and
	// the target
	let length = targetOffsetContainerTopOrLeft;

	// Adjust a little bit based on the alignment of the target
	if (targetPosition === 'center') {
		length += targetDimensions[widthOrHeight] / 2;
	} else if (targetPosition === 'bottom' || targetPosition === 'end') {
		length += targetDimensions[widthOrHeight];
	}

	// For start/center positions, we want contentArea to grow down/right from
	// the target, so we really need whatever space is remaining compared to above
	if (
		contentPosition === 'top' ||
		contentPosition === 'start' ||
		contentPosition === 'center'
	) {
		length = containerDimensions.scroll[widthOrHeight] - length;

		// Centered is a little special: we want to keep 50% of the content on
		// either side of the target
		if (contentPosition === 'center') {
			length += contentDimensions[widthOrHeight] / 2;
		}
	}

	// ensure contentArea is never bigger than the container, and is always
	// a whole number.
	return Math.floor(
		Math.min(length, containerDimensions.scroll[widthOrHeight])
	);
}

/**
 * Calculates either the top or left offset based on the target and content
 * positions and dimensions.
 * @param {Dimensions} targetDimensions - Target dimensions object plus offsets
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} contentAreaDimensions - Content area dimensions object
 * @param {Dimensions} containerDimensions - Container dimensions object
 * @param {("top"|"start"|"center"|"bottom"|"end")} targetPosition - Logical target position
 * @param {("top"|"start"|"center"|"bottom"|"end")} contentPosition - Logical content position
 * @param {("block"|"inline")} axis - Axis that is being calculated (block for top/bottom, inline for left/right)
 * @return {number} - the calculated offset top or left of the content area
 */
export function calculateContentAreaOffset(
	targetDimensions,
	contentDimensions,
	contentAreaDimensions,
	containerDimensions,
	targetPosition,
	contentPosition,
	axis
) {
	const {topOrLeft, widthOrHeight} = getAxisInfo(axis);

	// With absolute positioning, contentArea could start at any point inside the
	// container. Need to figure out the amount to shift the contentArea so that
	// it aligns to the container properly.
	//
	// Start by figuring out how to get the contentArea position to align
	// to the top/start of the container:
	let calcOffset =
		// Start with container's current position
		containerDimensions[topOrLeft] -
		// Add the scrolling offset. Dimensions values are relative to the
		// viewport, which doesn't know about any scrolling offset introduced by an
		// overflow'able container.
		containerDimensions.scroll[axis];

	// If contentArea is already stretched to be as big as the container, then we don't
	// need to do any other adjusting.
	if (
		contentAreaDimensions[widthOrHeight] <
		containerDimensions.scroll[widthOrHeight]
	) {
		// When content is top or start aligned, the content area starts at the
		// target position and grows towards the bottom/end of the container. Need
		// to increase the `calcOffset` by however much the target offset is.
		//
		// We'll skip over this adjustment in the case where content is bottom/end
		// aligned, because in these cases the contentArea starts at the top/start
		// point of the container, which we already figured out above.
		if (
			contentPosition === 'top' ||
			contentPosition === 'start' ||
			contentPosition === 'center'
		) {
			calcOffset +=
				targetDimensions[topOrLeft] -
				containerDimensions[topOrLeft] +
				containerDimensions.scroll[axis];

			// Centered is a little special: we want to keep 50% of the content on
			// either side of the target
			if (contentPosition === 'center') {
				calcOffset -= contentDimensions[widthOrHeight] / 2;
			}

			// Adjust a little bit based on the alignment of the target
			if (targetPosition === 'center') {
				calcOffset += targetDimensions[widthOrHeight] / 2;
			} else if (targetPosition === 'bottom' || targetPosition === 'end') {
				calcOffset += targetDimensions[widthOrHeight];
			}
		}
	}

	return Math.floor(calcOffset);
}

/**
 * Calculates a new dimensions object that describes the desired contentArea
 * size and position after it is placed relative to the target.
 * @param {Dimensions} targetDimensions - Target dimensions object plus offsets
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} contentAreaDimensions - Content area dimensions object
 * @param {Dimensions} containerDimensions - Container dimensions object
 * @param {("top"|"start"|"center"|"bottom"|"end")} targetPosition - Logical target position
 * @param {("top"|"start"|"center"|"bottom"|"end")} contentPosition - Logical content position
 * @return {Dimensions} - a new content area dimensions object
 */
export default function calculateContentAreaDimensions(
	targetDimensions,
	contentDimensions,
	contentAreaDimensions,
	containerDimensions,
	targetPosition,
	contentPosition
) {
	const [targetPositionBlock, targetPositionInline] = targetPosition;
	const [contentPositionBlock, contentPositionInline] = contentPosition;

	const nextContentAreaDimensions = {...contentAreaDimensions};
	nextContentAreaDimensions.width = calculateContentAreaLength(
		targetDimensions,
		contentDimensions,
		containerDimensions,
		targetPositionInline,
		contentPositionInline,
		'inline'
	);

	nextContentAreaDimensions.height = calculateContentAreaLength(
		targetDimensions,
		contentDimensions,
		containerDimensions,
		targetPositionBlock,
		contentPositionBlock,
		'block'
	);

	nextContentAreaDimensions.left = calculateContentAreaOffset(
		targetDimensions,
		contentDimensions,
		nextContentAreaDimensions,
		containerDimensions,
		targetPositionInline,
		contentPositionInline,
		'inline'
	);

	nextContentAreaDimensions.top = calculateContentAreaOffset(
		targetDimensions,
		contentDimensions,
		nextContentAreaDimensions,
		containerDimensions,
		targetPositionBlock,
		contentPositionBlock,
		'block'
	);

	nextContentAreaDimensions.right =
		nextContentAreaDimensions.left + nextContentAreaDimensions.width;
	nextContentAreaDimensions.bottom =
		nextContentAreaDimensions.top + nextContentAreaDimensions.height;

	return nextContentAreaDimensions;
}
