/**
 * Calculates special values of `constrain`
 */
export default function toSizeValue(contentValue, targetValue, constrainValue) {
	if (constrainValue === 'auto') {
		return contentValue;
	}
	if (constrainValue === 'target') {
		return targetValue;
	}
	if (typeof constrainValue === 'number') {
		return constrainValue;
	}
	throw new Error('Sizing value is not valid');
}
