function canScroll(overflow) {
	return overflow === 'auto' || overflow === 'hidden';
}

/**
 * Caches the result of a `getDimensions()` call so that it won't be
 * recalculated again later. Useful to ensure that getBoundingClientRect is
 * not called multiple times in a row when multiple fit positions are
 * evaluated in a row.
 * @param {Dimensions} dim
 * @return {{getDimensions: Function}}
 */
export function cacheDimensions(dim) {
	return {
		getDimensions: () => dim
	};
}

/**
 * Gets the dimensions of a HTMLElement or gets the manually specified and/or
 * cached dimensions of a DimensionsReference.
 * @param {(HTMLElement|DimensionsReference)} node
 * @return {Dimensions}
 */
export function getDimensions(node) {
	if (
		!(node instanceof HTMLElement) &&
		typeof node.getDimensions === 'function'
	) {
		return node.getDimensions();
	}
	if (
		node === window ||
		node === document.documentElement ||
		node === document.body
	) {
		return {
			top: 0,
			right: document.documentElement.clientWidth,
			bottom: document.documentElement.clientHeight,
			left: 0,
			width: document.documentElement.clientWidth,
			height: document.documentElement.clientHeight,
			existing: {
				top: 0,
				left: 0
			},
			scroll: {
				block: window.scrollY,
				inline: window.scrollX,
				height: document.documentElement.scrollHeight,
				width: document.documentElement.scrollWidth
			}
		};
	}

	// the fractional part of the getBoundingClientRect() values may cause
	// subtle differences to the rendered position after fit is run, which can
	// be pretty noticeable if we're constantly updating the fit position on
	// scroll. Rounding here helps to avoid this jitteriness.
	const box = node.getBoundingClientRect();
	const top = Math.round(box.top);
	const right = Math.round(box.right);
	const bottom = Math.round(box.bottom);
	const left = Math.round(box.left);
	const width = Math.round(box.width);
	const height = Math.round(box.height);
	const {scrollWidth, scrollHeight, scrollTop, scrollLeft} = node;
	const style = window.getComputedStyle(node);
	return {
		top,
		right,
		bottom,
		left,
		width,
		height,
		existing: {
			top: parseInt(style.top, 10) || 0,
			left: parseInt(style.left, 10) || 0
		},
		scroll: {
			// scrollWidth/height might be smaller than the getBoundingClientRect
			// reported values because scrollWidth/height returns rounded, whole
			// numbers and getBoundingClientRect returns fractions. However, we treat
			// these scroll properties as the largest possible size that the node can
			// take up, so we want to guarantee that these properties are always at
			// least the size of the getBoundingClientRect width/height.
			width: canScroll(style.overflowX) ? Math.max(scrollWidth, width) : width,
			height: canScroll(style.overflowY)
				? Math.max(scrollHeight, height)
				: height,
			block: scrollTop,
			inline: scrollLeft
		}
	};
}
