import calculateContentAreaDimensions from './content-area';
import calculateContentAreaStyles from './content-area-styles';
import calculateContentDimensions from './content';
import calculateContentStyles from './content-styles';
import calculateOverlap from './overlap';
import {normalizeConstrain, applyConstrain} from './constrain';

/**
 * Offset used to take an array of 6 values.
 * If someone passes in 6 values we will take the last four and apply them to the normOffset.
 * The functionality of this has not changed from V1.
 * We not longer support the first two values of the 6 value array. They didn't work as intended.
 * Instead we will convert the old two values to our new one value.
 */
const makeOffsetBackwardsCompatible = (offset) => {
	if (offset.length === 6) {
		return offset.slice(2, 6);
	} else {
		return offset.find((element) => element > 0);
	}
};

/**
 * Normalizes the offset format into `[top, end, bottom, start]`
 */
const normalizeOffset = (offset, targetPosition, contentPosition) => {
	let value = offset;
	if (Array.isArray(offset)) {
		const backwardCompatibleOffset = makeOffsetBackwardsCompatible(offset);
		if (Array.isArray(backwardCompatibleOffset)) {
			return backwardCompatibleOffset;
		} else value = backwardCompatibleOffset;
	}

	let calcOffset = [value, value, value, value];
	if (targetPosition[0] === contentPosition[0]) {
		calcOffset[0] = 0;
		calcOffset[2] = 0;
	}
	if (targetPosition[1] === contentPosition[1]) {
		calcOffset[1] = 0;
		calcOffset[3] = 0;
	}
	return calcOffset;
};

/**
 * Adds the offset to the target dimensions for positioning
 * @param {Dimensions} targetDimensions - Target dimensions
 * @param {Array<number>} normOffset - normalized offset into `[top, right, bottom, left]` format
 * @returns {Dimensions} - new dimensions with the normalized offset added in
 */
function addOffsetPaddingToDimensions(targetDimensions, normOffset) {
	const [top = 0, right = 0, bottom = 0, left = 0] = normOffset;
	return {
		...targetDimensions,
		top: targetDimensions.top - top,
		right: targetDimensions.right + right,
		bottom: targetDimensions.bottom + bottom,
		left: targetDimensions.left - left,
		width: targetDimensions.width + left + right,
		height: targetDimensions.height + top + bottom
	};
}

/**
 * Calculates dimensions for the content area and position of the content.
 * Returns the styles for the content area and the content, as well as a ratio
 * of how much of the content has been adjusted and/or is hidden by scrolling.
 * @param {Dimensions} targetDimensions - Target dimensions object
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} contentAreaDimensions - Content area dimensions object
 * @param {Dimensions} containerDimensions - Container dimensions object
 * @param {Dimensions} viewportDimensions - Viewport dimensions object
 * @param {Origin[]} targetPosition
 * @param {Origin[]} contentPosition
 * @param {number} offset - Amount to offset content from the target
 * @param {Constrain} constrain - Constrain configuration
 */
export default function calculateFit(
	targetDimensions,
	contentDimensions,
	contentAreaDimensions,
	containerDimensions,
	viewportDimensions,
	targetPosition,
	contentPosition,
	offset,
	constrain
) {
	const normOffset = normalizeOffset(offset, targetPosition, contentPosition);
	const normConstrain = normalizeConstrain(
		constrain,
		targetDimensions,
		contentDimensions
	);

	const constrainedContentDimensions = applyConstrain(
		normConstrain,
		contentDimensions
	);

	const paddedTargetDimensions = addOffsetPaddingToDimensions(
		targetDimensions,
		normOffset
	);

	const nextContentAreaDimensions = calculateContentAreaDimensions(
		paddedTargetDimensions,
		constrainedContentDimensions,
		contentAreaDimensions,
		containerDimensions,
		targetPosition,
		contentPosition
	);

	// updated content dimensions and overlap is only used to determine how much
	// of the content is actually going to be visible. We'll compare these values
	// when determining the best fit. These values are not necessary to calculate
	// the final CSS for the content element.

	const nextContentDimensions = calculateContentDimensions(
		constrainedContentDimensions,
		nextContentAreaDimensions,
		contentPosition
	);

	const {visibleContentDimensions, overlapRatios} = calculateOverlap(
		nextContentDimensions,
		nextContentAreaDimensions,
		viewportDimensions
	);

	const contentAreaStyles = calculateContentAreaStyles(
		contentAreaDimensions,
		nextContentAreaDimensions
	);
	const contentStyles = calculateContentStyles(contentPosition, normConstrain);

	return {
		style: {
			contentArea: contentAreaStyles,
			content: contentStyles
		},
		dimensions: {
			target: targetDimensions,
			content: visibleContentDimensions,
			contentArea: nextContentAreaDimensions,
			container: containerDimensions
		},
		overlapRatios,
		targetPosition,
		contentPosition
	};
}
