import toSizeValue from './to-size-value';

/**
 * Returns the dimension direction for a given constrain
 * key.
 * @returns {("width"|"height"|null)} returns "width" for width-related keys,
 * "height" for height-related keys, and null for any invalid key
 */
export function getDimensionForConstrainKey(key) {
	if (['width', 'minWidth', 'maxWidth'].includes(key)) {
		return 'width';
	}

	if (['height', 'minHeight', 'maxHeight'].includes(key)) {
		return 'height';
	}

	return null;
}

/**
 * Normalizes a constrain configuration by mapping all special keywords
 * (`auto`, `target`), etc to resolved numerical values. Also drops
 * any property keys that are not valid
 *
 * @param {Constrain} constrain - constrain object before normalization
 * @param {Dimensions} targetDimensions - target dimensions object
 * @param {Dimensions} contentDimensions - content dimensions object
 */
export function normalizeConstrain(
	constrain,
	targetDimensions,
	contentDimensions
) {
	return Object.entries(constrain).reduce((result, [key, constraint]) => {
		const widthOrHeight = getDimensionForConstrainKey(key);
		if (widthOrHeight) {
			result[key] = toSizeValue(
				contentDimensions.scroll[widthOrHeight],
				targetDimensions[widthOrHeight],
				constraint
			);
		}

		return result;
	}, {});
}

/**
 * Returns a new dimensions object with the constrain values applied.
 *
 * @param {Constrain} normalizedConstrain - constrain object after normalization
 * @param {Dimensions} dimensions - dimensions object to apply constrain to
 */
export function applyConstrain(normalizedConstrain, dimensions) {
	const newDimensions = Object.entries(normalizedConstrain).reduce(
		(result, [key, value]) => {
			const widthOrHeight = getDimensionForConstrainKey(key);
			if (key.startsWith('min')) {
				result[widthOrHeight] = Math.max(dimensions[widthOrHeight], value);
			} else if (key.startsWith('max')) {
				result[widthOrHeight] = Math.min(dimensions[widthOrHeight], value);
			} else {
				result[widthOrHeight] = value;
			}

			return result;
		},
		{...dimensions}
	);

	newDimensions.bottom = newDimensions.top + newDimensions.height;
	newDimensions.right = newDimensions.left + newDimensions.width;

	return newDimensions;
}
