/**
 * Creates an object of CSS properties to be applied to the contentArea element
 * @param {Dimensions} contentAreaDimensions - the current contentArea dimensions object,
 * before any fit calculations have been applied.
 * @param {Dimensions} nextContentAreaDimensions - the new contentArea dimensions object,
 * after all fit calculations have been applied.
 * @returns {Object} CSS properties that should be applied to contentArea.
 */
export default function calculateContentAreaStyles(
	contentAreaDimensions,
	nextContentAreaDimensions
) {
	// determine the distance we need to move the old contentArea to correctly
	// match the new contentArea. Since we're going to be setting these as inline
	// styles, we need to make sure we also consider any existing inline style
	// values for top and left, as these will get overwritten:
	//
	//	Example, if we have the following, relative to the top of the page:
	//
	//		<div id="content-area" style="top: 20px; left: 20px" />
	//
	//  And we want to position this contentArea to start at point (30, 30),
	//  our dimensions objects look like:
	//
	//		    contentAreaDimensions = { top: 20, left: 20 };
	//		nextContentAreaDimensions = { top: 30, left: 30 };
	//
	//  If we don't account for the existing inline styles, we would assume that
	//  we only need to move the contentArea 10px top and left, so we'd result
	//  in:
	//
	//		<div id="content-area" style="top: 10px; left: 10px" />
	//
	//	which is not correct. We need to make sure we also factor in the existing
	//	20px top/left that the contentArea started with. These values are tracked
	//	in the `existing` property of a dimensions object:
	const top =
		nextContentAreaDimensions.top -
		contentAreaDimensions.top +
		contentAreaDimensions.existing.top;
	const left =
		nextContentAreaDimensions.left -
		contentAreaDimensions.left +
		contentAreaDimensions.existing.left;
	return {
		top: `${top}px`,
		left: `${left}px`,
		width: `${nextContentAreaDimensions.width}px`,
		height: `${nextContentAreaDimensions.height}px`,
		position: 'absolute',
		pointerEvents: 'none'
	};
}
