import getAxisInfo from './get-axis-info';

/**
 * Calculates either the top or left coordinate for the content based on where
 * it would exist when placed in the new content area.
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} contentAreaDimensions - Content area dimensions object
 * @param {("top"|"start"|"center"|"bottom"|"end")} contentPosition - Logical content position
 * @param {("block"|"inline")} axis - Axis that is being calculated (block for top/bottom, inline for left/right)
 * @return {number} - the calculated offset top or left of the content area
 */
export function calculateContentOffset(
	contentDimensions,
	contentAreaDimensions,
	contentPosition,
	axis
) {
	const {topOrLeft, bottomOrRight, widthOrHeight} = getAxisInfo(axis);
	if (
		contentPosition === 'top' ||
		contentPosition === 'start' ||
		contentPosition === 'center'
	) {
		return contentAreaDimensions[topOrLeft];
	}

	return (
		contentAreaDimensions[bottomOrRight] - contentDimensions[widthOrHeight]
	);
}

/**
 * Calculates a new content dimensions object that describes where
 * the content would be after being placed inside the (correctly-positioned)
 * content area.
 *
 * Note that these values aren't necessarily the final values where content is
 * going to be rendered. It is the point where content would be if there were
 * no other boundaries (from the viewport, container, or both). This is useful
 * later to determine how much of the content will be visible and rank this
 * position against others in `getBestFitInfo`.
 *
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} contentAreaDimensions - Content area dimensions object
 * @param {("top"|"start"|"center"|"bottom"|"end")} contentPosition - Logical content position
 * @param {("block"|"inline")} axis - Axis that is being calculated (block for top/bottom, inline for left/right)
 * @return {Dimensions} - the updated content dimensions object as if it were placed
 * in a correctly-positioned contentArea and not bounded by any other containing object.
 */
export default function calculateContentDimensions(
	contentDimensions,
	contentAreaDimensions,
	contentPosition
) {
	const [contentPositionBlock, contentPositionInline] = contentPosition;

	const nextContentDimensions = {...contentDimensions};

	nextContentDimensions.top = calculateContentOffset(
		contentDimensions,
		contentAreaDimensions,
		contentPositionBlock,
		'block'
	);

	nextContentDimensions.left = calculateContentOffset(
		contentDimensions,
		contentAreaDimensions,
		contentPositionInline,
		'inline'
	);

	nextContentDimensions.bottom =
		nextContentDimensions.top + contentDimensions.height;
	nextContentDimensions.right =
		nextContentDimensions.left + contentDimensions.width;

	return nextContentDimensions;
}
