import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-popover-panel.scss';
import {actionTypes} from '@servicenow/ui-core';

const {COMPONENT_DOM_READY} = actionTypes;

const view = (state) => {
	const {tailDirection, disableScroll} = state.properties;
	return (
		<div
			id="fit-content-area"
			class={{
				'fit-content-area': true,
				'has-tail': tailDirection,
				[`-${tailDirection}`]: tailDirection,
				'disable-scroll': disableScroll
			}}>
			<div id="fit-content" className="fit-content">
				<slot name="content" />
			</div>
		</div>
	);
};

/**
 * Panel element that holds markup and styles required to correctly position
 * popover content relative to its trigger element.
 *
 * This component should not be used outside of the now-popover implementation.
 *
 * ```jsx
 * <now-popover-panel tail-direction="up">
 *   <now-my-dialog slot="content" />
 * </now-popover>
 * ```
 *
 * @seismicElement now-popover-panel
 * @private
 * @summary A container for popover content.
 */
createEnhancedElement('now-popover-panel', {
	properties: {
		/**
		 * Direction to display the tail.
		 * @type {"up"|"down"|"left"|"right"}
		 * @private
		 */
		tailDirection: {},

		/**
		 * Function to call once the panel has
		 * been rendered
		 * @type {(HTMLElement) => void}
		 * @private
		 */
		onReady: {},
		/**
		 * If true, content in the popover panel will not overflow with a scrollbar.
		 * Use this flag if your popover content already contains a custom
		 * scroll container for handling overflowed content.
		 * @type {boolean}
		 * @private
		 */
		disableScroll: {}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]({host, properties}) {
			properties.onReady(host);
		}
	},
	slots: {
		/**
		 * Contains popover content to float above other content on the
		 * page.
		 */
		content: {}
	},
	styles,
	view
});
