import isEmpty from 'lodash/isEmpty';
import forEachRight from 'lodash/forEachRight';
import debounce from 'lodash/debounce';

/**
 * Factory function to provide a way to have multiple callbacks triggered from
 * a single (optionally debounced) DOM event listener on the window.
 *
 * This has a couple of benefits over direct `addEventListeners`
 *
 * - Re-using a single event listener to run multiple callbacks leads to a
 *   performance gain in Chrome + Safari (Firefox doesn't seem to care much)
 *
 * - Callbacks can be deleted just by just ID, rather than needing to maintain
 *   a reference to the callback that was used in `addEventListener`
 *
 * - We can easily guarantee execution order when multiple instances listen
 *   to the same event.
 *
 * Callbacks are run in reverse order, so newer callbacks happen first. If any
 * callback returns an explicit `false`, other callbacks are not run.
 */
export default function createBatchedListener(eventType, {batch = false} = {}) {
	const callbacks = {};

	let runCallbacks = (event) =>
		forEachRight(callbacks, (callback) => callback(event));
	if (batch) {
		runCallbacks = debounce(runCallbacks, 500);
	}

	return {
		add(id, callback) {
			const needsToAttach = isEmpty(callbacks);
			callbacks[id] = callback;

			if (needsToAttach) {
				window.addEventListener(eventType, runCallbacks, true);
			}
		},

		remove(id) {
			delete callbacks[id];
			const canDetach = isEmpty(callbacks);

			if (canDetach) {
				window.removeEventListener(eventType, runCallbacks, true);
			}
		}
	};
}
