import isEmpty from 'lodash/isEmpty';
import isString from 'lodash/isString';
import isNumber from 'lodash/isNumber';
import isArray from 'lodash/isArray';
import mapValues from 'lodash/mapValues';
import normalizePosition from './normalize-position';

function positionToFitShorthand(position) {
	if (isString(position)) {
		return position;
	}

	const target = position.trigger || position.target;
	if (position.content) {
		return `${target} ${position.content}`;
	}

	return target;
}

/**
 * Popovers with tails default to centered positions.
 * Popovers without tails default to being aligned on some axis.
 */
function resolvePosition(hideTail, positions, dir) {
	// Always prefer the positions provided by the consumer
	if (Array.isArray(positions) && !isEmpty(positions)) {
		positions = positions.map(positionToFitShorthand);
	} else {
		positions = hideTail
			? [
					'bottom-start top-start',
					'bottom-end top-end',

					'top-start bottom-start',
					'top-end bottom-end',

					'top-end top-start',
					'bottom-end bottom-start',

					'top-start top-end',
					'bottom-start bottom-end'
			  ]
			: [
					'top-center bottom-center',
					'bottom-center top-center',
					'center-end center-start',
					'center-start center-end'
			  ];
	}

	return positions.map((position) => normalizePosition(position, dir));
}

function resolveOffset(hideTail, offset) {
	if (isNumber(offset) || isArray(offset)) {
		return offset;
	}
	return hideTail ? 0 : 12;
}

function resolveConstrain(constrain) {
	return mapValues(constrain, (value) =>
		value === 'trigger' ? 'target' : value
	);
}

/**
 * Compute properties that are important for fit, based on both the properties
 * that are passed in to the component and sane defaults which are different
 * whether or not we're rendering a tail.
 */
export default function resolveFitProperties({
	hideTail,
	positions,
	constrain,
	offset,
	dir
}) {
	return {
		positions: resolvePosition(hideTail, positions, dir),
		offset: resolveOffset(hideTail, offset),
		constrain: resolveConstrain(constrain)
	};
}
