import find from 'lodash/find';

const POPOVER_PANEL_TAG = 'NOW-POPOVER-PANEL';

export function getContent(host) {
	return host.querySelectorAll('[slot=content]');
}

export function getFirstTrigger(host) {
	return host.querySelector('[slot=trigger]');
}

/**
 * Checks if the given element is a popover panel that is nested within a
 * another panel.
 *
 * @param element - The element to check
 * @param parentPanel - The panel that may contain the element
 *
 * @returns {boolean} `true` if the element is itself a panel that is
 * originally contained within the `parentPanel`. Otherwise, returns `false`.
 */
export function isNestedPanel(element, parentPanel) {
	if (!element || !parentPanel || element.tagName !== POPOVER_PANEL_TAG) {
		return false;
	}

	let root = element.nowPopover;
	while ((root = root.getRootNode()) !== null) {
		if (!root.host) {
			return false;
		}

		if (parentPanel.contains(root.host)) {
			return true;
		}

		root = root.host;
	}
}

/**
 * Determine which slotted trigger was interacted with.
 */
export function findTriggerFromEvent(event, slot) {
	const assignedNodes = slot.assignedNodes({flatten: true});

	if (assignedNodes.length === 0) {
		return null;
	}

	if (assignedNodes.length === 1) {
		return assignedNodes[0];
	}

	// if there are multiple slotted triggers, the one that we want will be right
	// before the <slot> node in the composed path
	return find(event.composedPath(), (node, index, path) => {
		const nextNode = path[index + 1];
		return nextNode === slot;
	});
}

/**
 * Dynamically create a `<now-popover-panel />` element in the correct location
 * in the DOM and hoist slotted content to that element. We want Seismic to
 * control hoisting the content so that actions and events can be re-routed
 * correctly, but we still need want to include the now-popover-panel as a
 * parent to ensure we have the correct markup and styles required to use
 * getBestFitInfo.
 *
 * We also can't hoist the now-popover-panel directly, because it wouldn't be
 * able to take the content (slotted into the parent now-popover) along with
 * it.
 */
export async function hoist(host, container, nodes) {
	const panel = document.createElement(POPOVER_PANEL_TAG);

	// provide a reference back to the original <now-popover> from the panel
	// element. This is used so that we can figure out the original DOM order of
	// elements when we receive an event from the hoisted panel.
	panel.nowPopover = host;

	// Seismic will render the panel asynchronously, so we need to wait for that
	// to happen. Since we're creating the panel outside of a normal view
	// function/render hierarchy, we can't really listen for an action to be
	// dispatched from the panel. Instead, we have a custom callback function
	// that panel calls once it has been rendered :(
	return await new Promise((resolve) => {
		panel.onReady = resolve;

		container.append(panel);

		for (const node of nodes) {
			node.nowHoistAppendTo = panel;
			node.nowHoist = true;
		}
	});
}

export function unhoist(panel, content) {
	for (const node of content) {
		node.nowHoist = false;
	}

	if (panel) {
		panel.remove();
	}
}

/**
 * Attaches a listener to the container, then fires a fake event from the given element
 * to identify all ancestor elements in the element's DOM tree. More efficient than
 * climbing the tree recursively.
 * @param {string} id - unique ID
 * @param {HTMLElement} element - the element to find ancestors of
 * @param {HTMLElement} container - the topmost element to resolve to
 */
export const findAncestors = (id, element, container) =>
	new Promise((resolve) => {
		const eventName = `findAncestors-${id}`;
		const findListener = async (e) => {
			container.removeEventListener(eventName, findListener);
			const path = e.composedPath();
			const length = path.length;
			const ancestors = new Set(path);

			// This is gross but since popovers are hoisted,
			// any child popovers will lose context of their parent's
			// scroll containers. To fix this, we can lookup the parent
			// popover and recurse for its ancestors, then merge
			// the hoisted and non-hoisted ancestor lists. Ideally we
			// could memoize these but since we don't control data on
			// the host, it's risky to store the memoized nodes
			// in a public property.
			for (let idx = 0; idx < length; idx++) {
				const node = path[idx];
				if (
					node !== element &&
					node.nodeType === Node.ELEMENT_NODE &&
					node.tagName === POPOVER_PANEL_TAG
				) {
					const parentAncestors = await findAncestors(
						node.nowId,
						node.nowPopover,
						container
					);

					parentAncestors.forEach((ancestor) => ancestors.add(ancestor));
				}
			}

			resolve(ancestors);
		};

		container.addEventListener(eventName, findListener);
		const event = new CustomEvent(eventName, {
			bubbles: true,
			composed: true,
			cancelable: false
		});
		element.dispatchEvent(event);
	});
