/**
 * Restricts the content dimensions to the given bounding dimensions object
 * and returns both the ratio of overlap and the new dimensions.
 *
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} boundingDimensions - container dimensions or viewport dimensions, depending
 * on which bounding element to compare to.
 * @returns {{ratio: number, dimensions: Dimensions}}
 */
export function restrictToBoundingBox(contentDimensions, boundingDimensions) {
	const fullyVisible =
		contentDimensions.top >= boundingDimensions.top &&
		contentDimensions.right <= boundingDimensions.right &&
		contentDimensions.bottom <= boundingDimensions.bottom &&
		contentDimensions.left >= boundingDimensions.left;

	if (fullyVisible) {
		return {
			ratio: 1,
			dimensions: contentDimensions
		};
	}

	const visibleDimensions = {
		...contentDimensions,
		left: Math.max(contentDimensions.left, boundingDimensions.left),
		right: Math.min(contentDimensions.right, boundingDimensions.right),
		bottom: Math.min(contentDimensions.bottom, boundingDimensions.bottom),
		top: Math.max(contentDimensions.top, boundingDimensions.top)
	};

	visibleDimensions.width = visibleDimensions.right - visibleDimensions.left;
	visibleDimensions.height = visibleDimensions.bottom - visibleDimensions.top;
	const actualArea = visibleDimensions.width * visibleDimensions.height;
	const wantedArea = contentDimensions.width * contentDimensions.height;

	return {
		ratio: actualArea / wantedArea,
		dimensions: visibleDimensions
	};
}

/**
 * Calculates how much of the content would actually be viewable for the
 * given container and viewport.
 *
 * @param {Dimensions} contentDimensions - Content dimensions object
 * @param {Dimensions} containerDimensions - Container dimensions object
 * @param {Dimensions} viewportDimensions - Viewport dimensions object
 *
 * @typedef {Object} Overlap an object describing how much the content overlaps
 * with other boundary containers
 * @param {object} overlapRatios Object containing overlap ratios for the
 * container and viewport.
 * @param {number} overlapRatios.container The ratio of content that overlaps with the
 * container element. If the content completely fits in the container, this
 * value will be `1`. If the content falls off the container, this value will
 * be between 0 and 1.
 * @param {number} overlapRatios.viewport The ratio of content that overlaps with the
 * viewport element. If the content completely fits in the viewport, this
 * value will be `1`. If the content falls off the container, this value will
 * be between 0 and 1.
 * @param {Dimensions} visibleContentDimensions Updated content dimensions that
 * are bounded by the container. If the content fits completely in the container,
 * these dimensions will be identical to the original content dimensions. If the content
 * falls out of the container, then this dimensions object will be the overlap between
 * the content and the container.
 * @return {Overlap}
 */
export default function calculateOverlap(
	contentDimensions,
	contentAreaDimensions,
	viewportDimensions
) {
	const contentInContainer = restrictToBoundingBox(
		contentDimensions,
		contentAreaDimensions
	);
	const contentInViewport = restrictToBoundingBox(
		contentDimensions,
		viewportDimensions
	);

	return {
		overlapRatios: {
			container: contentInContainer.ratio,
			viewport: contentInViewport.ratio
		},
		visibleContentDimensions: contentInContainer.dimensions
	};
}
