import {getDimensions, cacheDimensions} from './dimensions';
import calculateFit from './calculate-fit';

export function sortCandidates(a, b) {
	const viewportOverlapDifference =
		b.overlapRatios.viewport - a.overlapRatios.viewport;

	// first priority is to pick the position that fits the most content within
	// the viewport: we want the content to be immediately visible. If multiple
	// positions can fit the same amount of content in the viewport, then as a
	// tie-breaker we'll use the position that fits more within the container (to
	// minimize how much scrolling of the content is necessary).
	if (viewportOverlapDifference === 0) {
		const containerOverlapDifference =
			b.overlapRatios.container - a.overlapRatios.container;

		return containerOverlapDifference;
	}

	return viewportOverlapDifference;
}

/**
 * Calculates the fit styles for the content with only one possible position.
 * @param {HTMLElement} target - Reference to element the content is positioned beside
 * @param {HTMLElement} content - Reference to positioned element
 * @param {HTMLElement} [container=window] - Reference to element content should fit within
 * @param {string[][]} - Desired position of content relative to target, e.g. [["top", "start"], ["bottom", "end"]]
 * @param {number} [offset=0] - Amount to offset content from the target
 * @param {{height: number, width: number, minHeight: number, maxHeight: number, minWidth: number, maxWidth: number}} constrain - Constrains or sets the size of the content
 * @return {FitInfo}
 */
export function getFitInfo({
	target,
	content,
	contentArea,
	container = window,
	viewport = window,
	position,
	offset = 0,
	constrain = {}
}) {
	const targetDimensions = getDimensions(target);
	const contentDimensions = getDimensions(content);
	const containerDimensions = getDimensions(container);
	const contentAreaDimensions = getDimensions(contentArea);
	const viewportDimensions = getDimensions(viewport);
	const [targetPosition, contentPosition] = position;
	return calculateFit(
		targetDimensions,
		contentDimensions,
		contentAreaDimensions,
		containerDimensions,
		viewportDimensions,
		targetPosition,
		contentPosition,
		offset,
		constrain
	);
}

/**
 * Calculates the best fit styles for the content based on multiple possible
 * positions. Returns the fit info for the first position that completely fits.
 * If no position completely fits, returns the position that fits the most.
 * @param {HTMLElement} target - Reference to element the content is positioned beside
 * @param {HTMLElement} content - Reference to positioned element
 * @param {HTMLElement} [container=window] - Reference to element content should fit within
 * @param {string[][][]}- Desired positions of content relative to target in order of preferences, e.g. [[["bottom", "start"],["top", "start"]], [["top", "start"], ["bottom", "start"]]]
 * @param {number} [offset=0] - Amount to offset content from the target
 * @param {{height: number, width: number, minHeight: number, maxHeight: number, minWidth: number, maxWidth: number}} constrain - Constrains or sets the size of the content
 * @return {FitInfo}
 */
export default function getBestFitInfo({
	target,
	content,
	contentArea,
	container = window,
	viewport = window,
	positions,
	offset = 0,
	constrain = {}
}) {
	const targetDimensions = cacheDimensions(getDimensions(target));
	const contentDimensions = cacheDimensions(getDimensions(content));
	const contentAreaDimensions = cacheDimensions(getDimensions(contentArea));
	const containerDimensions = cacheDimensions(getDimensions(container));
	const viewportDimensions = cacheDimensions(getDimensions(viewport));
	const candidates = [];
	for (const pos of positions) {
		const info = getFitInfo({
			target: targetDimensions,
			content: contentDimensions,
			container: containerDimensions,
			contentArea: contentAreaDimensions,
			viewport: viewportDimensions,
			position: pos,
			offset,
			constrain
		});

		// Entire content fits in this position, no need to try other options
		if (
			info.overlapRatios.container === 1 &&
			info.overlapRatios.viewport === 1
		) {
			return info;
		}

		candidates.push(info);
	}

	/* Sort to find highest visible ratio, aka the one that fits the most content */
	candidates.sort(sortCandidates);

	return candidates[0];
}
